/*================================================================
[Define variables]
==================================================================*/
/*==================================================================*/
:root {
  --xe_primary_color: #ef2c2c;
  --xe_seconary_color: #1e73be;
  --xe_base_text_color: #585858;
  --xe_base_heading_color: #4A4A4A;
  --xe_base_link_color: #0366E0;
  --xe_body_background_color: #ffffff;
  --xe_border_color: #eeeeee;
  --xe_inputs_border_color: #dedede;
  --xe_button_text_color: white;
  --xe_button_background_color: #ef2c2c;
  --xe_button_hover_text_color: #ffffff;
  --xe_button_hover_background_color: #1e73be;
  --xe_button_is_link_style_text_color: #ef2c2c;
  --xe_button_is_link_style_hover_text_color: #1e73be;
  --xe_button_is_link_style_underline_color: #ef2c2c;
  --xe_button_is_link_style_hover_underline_color: #1e73be;
  --xe_page_header_breadcrumb_background_color: #EEEEEE;
  --xe_woo_on_sale_text_color: #ffffff;
  --xe_woo_on_sale_background_color: red;
  --xe_woo_current_price_text_color: red;
  --xe_woo_previous_price_text_color: #3a2e2e;
  --xe_system_font_family: -apple-system, BlinkMacSystemFont, Helvetica, Arial, Segoe ui, System ui, sans-serif;
  --xe_text_emphasis_font_family: "Playfair Display", Georgia, Garamond, Baskerville, Baskerville Old Face, Hoefler Text, Times New Roman, Serif;
  --xe_monospace_font_family: monospace, monospace;
  --xe_base_font_size: 0.9375rem;
  --xe_base_font_weight: normal;
  --xe_base_letter_spacing: 0;
  --xe_base_font_line_height: 1.6;
  --xe_base_font_family: "Inter", sans-serif;
  --xe_heading_font_weight: bold;
  --xe_heading_letter_spacing: 0;
  --xe_heading_line_height: 1.2;
  --xe_heading_font_family: "Inter", sans-serif;
  --xe_button_font_size: 0.9375rem;
  --xe_button_font_weight: normal;
  --xe_button_line_height: 1;
  --xe_woo_product_title_size: 0.9375rem;
  --xe_buttons_border_radius: 3px;
  --xe_inputs_border_radius: 4px;
  --xe_grid_gutter_width: 30px;
  --xe_grid_gutter_width_none: 0;
  --xe_container_width: 1400px;
  --xe_container_width_narrow: 800px;
  --xe_container_width_wide: 100%;
  --xe_large_device_breakpoint: 1024px;
  --xe_medium_device_breakpoint: 991px;
  --xe_tablet_device_breakpoint: 768px;
  --xe_mobile_device_breakpoint: 575px;
  --xe_mobile_device_gutter_width: 5px;
  --xe_narrow_section_base_width: 700px;
  --xe_default_primary_area_width: 100%;
  --xe_default_sidebar_width: 30%;
  --xe_boxed_layout_maxwidth: 1170px;
  --xe_no_sidebar_narrow_container_width: 800px;
  --xe_default_boxed_layout_container_maxwidth: 1170px;
  --xe_default_layout_container_stretch_width: 100%;
  --xe_default_layout_container_stretch_maxwidth: 1920px;
  --xe_mobile_menu_breakpoint: 991px;
  --xe_block_standard_spacing: 60px;
  --xe_woo_product_item_spacing: 30px;
  --xe_iconfont: "themify";
  --xe_mobile_device_woo_product_title_font_size: 14px;
  --xe_site_navigation_zindex: 10000000;
  --xe_submenu_dropdown_zindex: 10000001;
}

/* -------------------------------------------------------------------------- 
	#. Import variables & Mixins
   -------------------------------------------------------------------------- */
/* -------------------------------------------------------------------------- 
	#. Dokan ganeral styles
   -------------------------------------------------------------------------- */
.theme-xenial.dokan-dashboard .dokan-dashboard-header .entry-title {
  font-size: 24px;
  font-weight: normal;
}

/* -------------------------------------------------------------------------- 
	#. Dokan buttons
   -------------------------------------------------------------------------- */
.theme-xenial .dokan-btn,
.theme-xenial a.dokan-btn,
.theme-xenial input[type=submit].dokan-btn {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
  font-size: var(--xe_button_font_size);
  font-weight: normal;
  color: var(--xe_button_text_color);
  line-height: var(--xe_button_line_height);
  background-color: var(--xe_button_background_color);
  border: none !important;
  border-radius: var(--xe_buttons_border_radius);
}

.theme-xenial .dokan-btn:hover,
.theme-xenial a.dokan-btn:hover,
.theme-xenial input[type=submit].dokan-btn:hover {
  color: var(--xe_button_hover_text_color);
  border: none;
  background-color: var(--xe_button_hover_background_color);
}

.theme-xenial .dokan-pro-gravatar-drag,
.theme-xenial .dokan-btn.dokan-add-new-product {
  padding: 12px 15px;
}

/* -------------------------------------------------------------------------- 
	#. Dokan forms/input
   -------------------------------------------------------------------------- */
.theme-xenial .dokan-dashboard-wrap select.dokan-form-control {
  font-size: 15px;
  line-height: 26px;
  font-weight: normal;
  border-color: var(--xe_inputs_border_color);
  height: 48px;
  padding: 12px 15px;
  min-width: 200px;
  border-radius: 3px;
  background-image: url("data:image/svg+xml,%0A%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' width='512' height='512'%3E%3Cg id='_01_align_center' data-name='01 align center'%3E%3Cpath d='M12,15.5a1.993,1.993,0,0,1-1.414-.585L5.293,9.621,6.707,8.207,12,13.5l5.293-5.293,1.414,1.414-5.293,5.293A1.993,1.993,0,0,1,12,15.5Z'/%3E%3C/g%3E%3C/svg%3E%0A");
}

.theme-xenial .dokan-dashboard-wrap select.dokan-form-control:focus {
  border-color: #3da6ff;
}

/* -------------------------------------------------------------------------- 
	#. Dokan table
   -------------------------------------------------------------------------- */
.product-listing-table [data-title=Name] strong {
  display: block;
  margin-bottom: 5px;
  font-size: var(--xe_base_font_size);
  font-weight: var(--xe_base_font_weight);
}

.product-listing-table [data-title=Name] strong > a {
  color: var(--xe_base_text_color);
}

.product-listing-table [data-title=Name] strong > a:hover {
  color: var(--xe_seconary_color);
}

.theme-xenial .dokan-product-listing .dokan-product-listing-area table.product-listing-table mark.instock {
  font-size: 15px;
  font-weight: normal;
  line-height: 1;
}

.dokan-product-listing .dokan-product-listing-area del .amount,
.dokan-product-listing .dokan-product-listing-area ins .amount {
  font-family: var(--xe_system_font_family);
  font-weight: normal;
}

.dokan-product-listing .dokan-product-listing-area ins {
  background-color: transparent;
}

.theme-xenial .pagination-wrap ul.pagination,
.theme-xenial .dokan-pagination-container .dokan-pagination {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 30px;
}

.theme-xenial .pagination-wrap ul.pagination > li,
.theme-xenial .dokan-pagination-container .dokan-pagination li {
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 3px;
}

.pagination-wrap ul.pagination > li > a,
.pagination-wrap ul.pagination > li > span,
.theme-xenial .dokan-pagination-container .dokan-pagination li a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 40px;
  font-size: 13px;
  font-weight: 500;
  line-height: 40px;
  border-radius: 3px;
  margin: 0;
  color: var(--xe_base_text_color);
  border: 2px solid transparent;
}

.theme-xenial .pagination-wrap ul.pagination > li > a,
.theme-xenial .dokan-pagination-container .dokan-pagination li a {
  float: none;
  display: inline-flex;
  margin: 0;
  border: none;
  background-color: transparent;
}

.theme-xenial .pagination-wrap ul.pagination > li > .page-numbers:hover,
.theme-xenial .dokan-pagination-container .dokan-pagination li a:hover {
  color: var(--xe_seconary_color);
  text-decoration: none;
  background-color: transparent;
}

.theme-xenial .pagination-wrap ul.pagination > li > .page-numbers.current,
.theme-xenial .dokan-pagination-container .dokan-pagination li.active a {
  color: var(--xe_primary_color);
  background-color: transparent;
  border: 2px solid var(--xe_primary_color);
}

.theme-xenial .pagination-wrap ul.pagination > li > .page-numbers.next,
.theme-xenial .pagination-wrap ul.pagination > li > .page-numbers.prev {
  font-size: var(--xe_base_font_size);
  font-weight: normal;
  width: auto;
}